import Vue from "vue";
import Router from "vue-router";
import Store from "@/store";
import firebase from "firebase";
import Base from "@/views/client/Base.vue";
import Landing from "@/views/client/Landing.vue";

import Login from "@/views/admin/Login.vue";
import Admin from "@/views/admin/Base.vue";
import Dashboard from "@/views/admin/Dashboard.vue";
import Blogs from "@/views/admin/blog/Blogs.vue";
import CreateBlog from "@/views/admin/blog/CreateBlog.vue";
import Events from "@/views/admin/Events.vue";
import Members from "@/views/admin/Members.vue";
import Announcements from "@/views/admin/Announcements.vue";

import "iview/dist/styles/iview.css";

Vue.use(Router);

const router = new Router({
  mode: "hash",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "Base",
      component: Base,
      redirect: "/home",
      children: [
        {
          path: "home",
          name: "Welcome",
          component: Landing
        },
        {
          path: "about-us",
          name: "About Us",
          component: () => import("../views/client/AboutUs")
        },
        {
          path: "projects",
          name: "Projects",
          component: () => import("../views/client/Projects")
        },
        {
          path: "services",
          name: "Services",
          component: () => import("../views/client/Services")
        },
        {
          path: "partner",
          name: "Partner",
          component: () => import("../views/client/Partner")
        },
        {
          path: "sponsor",
          name: "Sponsor",
          component: () => import("../views/client/Sponsor")
        },
        {
          path: "updates",
          name: "Updates",
          component: () => import("../views/client/Updates")
        },
        {
          path: "upcoming",
          name: "Upcoming",
          component: () => import("../views/client/Upcoming")
        },
        {
          path: "accomplishment",
          name: "Accomplishment",
          component: () => import("../views/client/Accomplishment")
        },
        {
          path: "stories",
          name: "Stories",
          component: () => import("../views/client/Stories")
        },
        {
          path: "join-us",
          name: "Join Us",
          component: () => import("../views/client/JoinUs")
        },
        {
          path: "membership",
          name: "Membership",
          component: () => import("../views/client/Membership")
        },
        {
          path: "volunteer",
          name: "Volunteer",
          component: () => import("../views/client/Volunteer")
        },
        {
          path: "fundraising",
          name: "Fundraising",
          component: () => import("../views/client/Fundraising")
        },
        {
          path: "contact-us",
          name: "ContactUs",
          component: () => import("../views/client/ContactUs")
        }
      ]
    },
    {
      path: "/admin/login",
      name: "AdminLogin",
      component: Login,
      beforeEnter(to, from, next) {
        //if trying to visit admin login while authenticated redirect to admin dashboard
        let currentUser = Store.getters["currentUser/getCurrentUser"];
        if (to.name == "AdminLogin") {
          if (currentUser) {
            if (currentUser.userType == "admin") next("/admin/dashboard");
            else next("/home");
          } else {
            next();
          }
        }
      }
    },
    {
      path: "/admin",
      name: "Admin",
      component: Admin,
      redirect: "/admin/login",
      children: [
        {
          path: "dashboard",
          name: "Dashboard",
          component: Dashboard,
          meta: {
            auth: 1
          }
        },
        {
          path: "announcements",
          name: "Announcements",
          component: Announcements,
          meta: {
            auth: 1
          }
        },
        {
          path: "blogs",
          name: "Blogs",
          component: Blogs,
          meta: {
            auth: 1
          }
        },
        {
          path: "blogs/new",
          name: "CreateBlog",
          component: CreateBlog,
          meta: {
            auth: 1
          }
        },
        {
          path: "events",
          name: "Events",
          component: Events,
          meta: {
            auth: 1
          }
        },
        {
          path: "members",
          name: "Members",
          component: Members,
          meta: {
            auth: 1
          }
        }
      ]
    }
  ]
});

router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  if (to.meta.auth) {
    if (currentUser) {
      next();
      return;
    } else {
      next("/admin/login");
      return;
    }
  }
  next();
});

router.afterEach(route => {
  // Do something..
});

export default router;
