<template>
  <div class="admin-blogs">
    <h3>Blogs</h3>
    <div class="cards-wrapper">
      <Row :gutter="20">
        <i-col span="8">
          <Card dis-hover>
            <div class="card-wrapper">
              <div class="card-icon">
                <Icon type="ios-checkbox-outline" size="60" />
              </div>
              <div class="card-info">
                <small>Blog types</small>
                <h1>999</h1>
              </div>
            </div>
          </Card>
        </i-col>
        <i-col span="8">
          <Card dis-hover>
            <div class="card-wrapper">
              <div class="card-icon">
                <Icon type="ios-document-outline" size="60" />
              </div>
              <div class="card-info">
                <small>Active blogs</small>
                <h1>999</h1>
              </div>
            </div>
          </Card>
        </i-col>
        <i-col span="8">
          <Card dis-hover>
            <div class="card-wrapper">
              <div class="card-icon">
                <Icon type="ios-trash-outline" size="60" />
              </div>
              <div class="card-info">
                <small>Archived blogs</small>
                <h1>999</h1>
              </div>
            </div>
          </Card>
        </i-col>
      </Row>
    </div>
    <Card class="blogs-card" dis-hover style="height: 100%;">
      <div slot="title">
        <div class="table-header">
          <div>
            <Checkbox></Checkbox>
          </div>
          <div>
            <small>
              <strong>Title</strong>
            </small>
          </div>
          <div>
            <small>
              <strong>Content</strong>
            </small>
          </div>
          <div>
            <small>
              <strong>Posted by</strong>
            </small>
          </div>
          <div>
            <small>
              <strong>Date Posted</strong>
            </small>
          </div>
          <div>
            <Page :total="100" simple />
          </div>
        </div>
      </div>
      <div class="table-content">
        <div class="table-center" v-if="isLoadingBlogs">
          <Spin size="large" />
        </div>
        <div v-else>
          <div
            class="table-center"
            v-if="getAllPosts && getAllPosts.length < 1"
          >
            Nothing to show here
          </div>
          <div
            v-else
            v-for="(post, p) in getAllPosts"
            :key="p"
            class="blog-item"
          >
            <div>
              <Checkbox></Checkbox>
            </div>
            <div>{{ post.title }}</div>
            <div>{{ stripHTML(post.content) }}</div>
            <div>
              <a href="#" class="truncate">{{ post.posterId }}</a>
            </div>
            <div>{{ post.createdAt.seconds | moment("MMMM DD, YYYY") }}</div>
            <div>
              <Button type="primary" icon="ios-create-outline" />
              <Button type="error" icon="ios-trash-outline" />
            </div>
          </div>
        </div>
      </div>
    </Card>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Utils from "@/helpers/utils";
export default {
  name: "Blogs",
  data() {
    return {
      isLoadingBlogs: false
    };
  },
  mounted() {
    this.getAllBlogs();
  },
  computed: {
    ...mapGetters("blog", ["getAllPosts"])
  },
  methods: {
    ...mapActions("blog", ["getAllBlogPosts"]),
    getAllBlogs() {
      this.isLoadingBlogs = true;
      this.getAllBlogPosts().finally(() => (this.isLoadingBlogs = false));
    },
    stripHTML(html) {
      return Utils.stripHTML(html);
    }
  }
};
</script>

<style lang="scss" scoped>
.admin-blogs {
  padding: 10px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.card-icon {
  width: 50%;
  display: flex;
  align-items: center;
}
.card-info {
  width: 50%;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
}
.card-wrapper {
  display: flex;
}
.cards-wrapper {
  margin: 10px 0;
}
.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
}
.table-header {
  div {
    text-align: start;
  }
  div:first-child {
    width: 5%;
  }
  div:not(:first-child) {
    width: 16%;
  }
}
.table-content {
  height: calc(100% - 58px);
  position: absolute;
  width: 100%;
  overflow-y: auto;
}
.blog-item {
  border-bottom: 1px solid #e8eaec;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  div {
    text-align: start;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  div:first-child {
    width: 5%;
  }
  div:not(:first-child) {
    width: 16%;
  }
  div:last-child {
    display: flex;
    align-items: center;
    justify-content: center;
    /deep/button:first-child {
      margin-right: 10px;
    }
  }
}
/deep/.blogs-card {
  .ivu-card-body {
    padding: 0px;
  }
}
.table-center {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
