import Vue from "vue";
import firebase from "firebase";
import Utils from "@/helpers/utils";

export default {
  namespaced: true,
  state: {
    showLoginModal: false,
    showForgotPassword: false
  },
  getters: {
    getShowLoginModal: state => {
      return state.showLoginModal;
    },
    getShowForgotPassword: state => {
      return state.showForgotPassword;
    }
  },
  mutations: {
    setShowLoginModal(state, v) {
      Vue.set(state, "showLoginModal", v);
    },
    setShowForgotPassword(state, v) {
      Vue.set(state, "showForgotPassword", v);
    }
  },
  actions: {
    firebaseSignup({ state, commit, dispatch }, { email, password }) {
      return new Promise((resolve, reject) => {
        firebase
          .auth()
          .createUserWithEmailAndPassword(email, password)
          .then(
            res => {
              let logUser = {
                email: res.user.email,
                uid: res.user.uid,
                id: res.user.uid,
                userType: "member"
              };
              dispatch("currentUser/createCurrentUser", logUser, { root: 1 });
              resolve(res.user);
            },
            err => {
              reject(err);
            }
          );
      });
    },
    firebaseLogin({ state, commit, dispatch }, { email, password }) {
      return new Promise((resolve, reject) => {
        firebase
          .auth()
          .signInWithEmailAndPassword(email, password)
          .then(
            res => {
              let logUser = {
                email: res.user.email,
                uid: res.user.uid,
                id: res.user.uid,
                userType: "member"
              };
              dispatch("currentUser/createCurrentUser", logUser, { root: 1 });
              resolve(res.user);
            },
            err => {
              reject(err);
            }
          );
      });
    },
    firebaseLogout({ state, commit }) {
      return new Promise((resolve, reject) => {
        firebase
          .auth()
          .signOut()
          .then(
            res => {
              commit("currentUser/setUser", null, { root: 1 });
              resolve(res);
            },
            err => {
              reject(err);
            }
          );
      });
    },
    firebasePasswordReset({ commit }, { email }) {
      return new Promise((resolve, reject) => {
        firebase
          .auth()
          .sendPasswordResetEmail(email)
          .then(function() {
            resolve();
          })
          .catch(function(error) {
            reject(error);
            switch (error.code) {
              case "auth/invalid-email":
                Utils.showMessage("Invalid Email", "error");
                break;
              case "auth/user-not-found":
                Utils.showMessage("Email not found", "error");
                break;
              default:
                break;
            }
          });
      });
    }
  }
};
