<template>
  <span class="time">
    {{ date | moment(format) }}
  </span>
</template>

<script>
export default {
  name: "time",
  props: {
    date: {
      type: Date,
      required: true
    },
    format: {
      type: String,
      default: "MMMM DD, YYYY"
    }
  }
};
</script>
