<template>
  <Modal v-model="show" title="" :mask-closable="false" :footer-hide="true">
    <Tabs v-model="tab">
      <TabPane label="Log-in" name="login"
        ><login-component ref="loginRef"
      /></TabPane>
      <TabPane label="Sign-up" name="signup"
        ><signup-component ref="signUpRef"
      /></TabPane>
    </Tabs>
  </Modal>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
import LoginComponent from "@/components/client/Login";
import SignupComponent from "@/components/client/Signup";
export default {
  name: "modal-login",
  components: { LoginComponent, SignupComponent },
  data() {
    return {
      modal1: true,
      tab: "login"
    };
  },
  watch: {
    show(n, o) {
      if (!n) {
        this.resetValues();
      }
    },
    tab(n, o) {
      if (o == "login") {
        if (this.$refs.loginRef) {
          this.$refs.loginRef.resetData();
        }
      } else if (o == "signup") {
        if (this.$refs.signUpRef) {
          this.$refs.signUpRef.resetData();
        }
      }
    }
  },
  methods: {
    ...mapMutations("auth", ["setShowLoginModal"]),
    resetValues() {
      this.tab = "login";
      if (this.$refs.loginRef) {
        this.$refs.loginRef.resetData();
      }
      if (this.$refs.signUpRef) {
        this.$refs.signUpRef.resetData();
      }
    },
    ok() {
      this.$Message.info("Clicked ok");
    },
    cancel() {
      this.$Message.info("Clicked cancel");
    }
  },
  computed: {
    ...mapGetters("auth", ["getShowLoginModal"]),
    show: {
      get() {
        return this.getShowLoginModal;
      },
      set(v) {
        this.setShowLoginModal(v);
      }
    }
  }
};
</script>

<style></style>
