<template>
  <div class="forgot-password" style="padding:8px 50px;text-align:center">
    <h3>Forgot Password?</h3>
    <div class="forgot-adjustments">
      Don't worry! Resetting your password is easy. Just type the email you
      registered in CoinServes.
    </div>
    <div class="forgot-adjustments" style="text-align:left">
      <b>Email</b>
      <Input
        v-model="email"
        placeholder="Enter your email address"
        @on-enter="resetPassword()"
      />
    </div>
    <Button
      @click="resetPassword()"
      class="forgot-adjustments"
      style="width:100%"
      type="primary"
      >Send</Button
    >
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Utils from "@/helpers/utils";
export default {
  data() {
    return {
      email: ""
    };
  },
  methods: {
    ...mapActions("auth", ["firebasePasswordReset"]),
    resetPassword() {
      this.firebasePasswordReset({ email: this.email }).then(
        res => {
          Utils.showMessage("Email has been sent to " + this.email, "success");
          this.resetSuccess();
          console.log({ res });
        },
        err => {
          console.log({ err });
        }
      );
    },
    resetFields() {
      this.email = "";
    },
    resetSuccess() {
      this.resetFields();
      this.$emit("resetSuccess");
    }
  }
};
</script>

<style lang="scss" scoped>
.forgot-password {
  padding: 8px 50px;
  text-align: center;
  .forgot-adjustments {
    margin-top: 16px;
  }
}
</style>
