import Vue from "vue";
import db from "@/firebase/db";
import Utils from "@/helpers/utils";

export default {
  namespaced: true,
  state: {
    allPosts: null,
    myPosts: null
  },
  getters: {
    getAllPosts: state => {
      return state.allPosts;
    },
    getMyPosts: state => {
      return state.myPosts;
    }
  },
  mutations: {
    setAllPosts(state, v) {
      Vue.set(state, "allPosts", v);
    },
    setMyPosts(state, v) {
      Vue.set(state, "myPosts", v);
    }
  },
  actions: {
    createBlogPost({ commit, state, dispatch, rootState }, params) {
      if (!params.uid) {
        params.uid = window.generateUUID();
      }
      params = {
        ...Utils.getBlogPostBase(),
        ...{ createdAt: new Date(), posterId: rootState.currentUser.user.uid },
        ...params
      };
      return new Promise((resolve, reject) => {
        db.getAllBlog()
          .doc(params.uid)
          .set(params)
          .then(res => {
            console.log(res);
            resolve(res);
          })
          .catch(err => {
            console.log(err);
            reject(err);
          });
      });
    },
    updateBlogPost({ commit, state, dispatch }, params) {
      params = { ...params, ...{ updateAt: new Date() } };
      return new Promise((resolve, reject) => {
        db.getAllBlog()
          .doc(params.uid)
          .update(params)
          .then(res => {
            console.log(res);
            resolve(res);
          })
          .catch(err => {
            console.log(err);
            reject(err);
          });
      });
    },
    deleteBlogPost({ commit, state, dispatch }, id) {
      let params = { deletedAt: new Date(), isDeleted: true };
      return new Promise((resolve, reject) => {
        db.getAllBlog()
          .doc(id)
          .update(params)
          .then(res => {
            console.log(res);
            resolve(res);
          })
          .catch(err => {
            console.log(err);
            reject(err);
          });
      });
    },
    hardDeleteBlogPost({ commit, state, dispatch }, id) {
      return new Promise((resolve, reject) => {
        db.getAllBlog()
          .doc(id)
          .delete()
          .then(res => {
            console.log(res);
            resolve(res);
          })
          .catch(err => {
            console.log(err);
            reject(err);
          });
      });
    },
    getAllBlogPosts({ commit }) {
      return new Promise((resolve, reject) => {
        db.getAllBlog()
          .where("isDeleted", "==", false)
          .get()
          .then(
            res => {
              let data = res.docs.map(doc => {
                let docData = doc.data();
                docData.id = doc.id;
                return docData;
              });
              console.log("All Blog Posts", data);
              commit("setAllPosts", data);
              resolve(data);
            },
            err => {
              console.log({ err });
              reject(err);
            }
          );
      });
    },
    getMyBlogPosts({ commit }, userId) {
      return new Promise((resolve, reject) => {
        db.getAllBlog()
          .where("isDeleted", "==", false)
          .where("posterId", "==", userId)
          .get()
          .then(
            res => {
              let data = res.docs.map(doc => {
                let docData = doc.data();
                docData.id = doc.id;
                return docData;
              });
              console.log("My Blog Posts", data);
              commit("setMyPosts", data);
              resolve(data);
            },
            err => {
              console.log({ err });
              reject(err);
            }
          );
      });
    }
  }
};
