import Vue from "vue";
import db from "@/firebase/db";

export default {
  namespaced: true,
  state: {
    user: null
  },
  getters: {
    getCurrentUser: state => {
      return state.user;
    }
  },
  mutations: {
    setUser(state, v) {
      Vue.set(state, "user", v);
    }
  },
  actions: {
    createCurrentUser({ commit, state, dispatch }, params) {
      params = { ...params, ...{ createdAt: new Date() } };
      db.getAllUsers()
        .where("uid", "==", params.uid)
        .get()
        .then(user => {
          if (!(user && !user.empty)) {
            db.getAllUsers()
              .doc(params.uid)
              .set(params)
              .then(res => {
                dispatch("getCurrentUser", params.uid);
              })
              .catch(function(error) {
                console.error("error: ", error);
              });
          }
        });
    },
    updateCurrentUser({ commit, state, dispatch }, params) {
      db.getAllUsers()
        .doc(params.uid)
        .update(params)
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getCurrentUser({ commit, state, dispatch }, id) {
      db.getUser(id).onSnapshot(
        res => {
          console.log("Current User", res.data());
          commit("setUser", res.data());
        },
        err => {
          console.log({ err });
        }
      );
    }
  }
};
