<template>
  <div class="modal-forgot-password-admin">
    <Modal title="" v-model="show" :mask-closable="false" :footer-hide="true">
      <ForgotPassword ref="forgotPassword" @resetSuccess="resetSuccess()" />
    </Modal>
  </div>
</template>

<script>
import ForgotPassword from "@/components/commons/ForgotPassword";
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "modal-forgot-password-admin",
  components: { ForgotPassword },
  computed: {
    ...mapGetters("auth", ["getShowForgotPassword"]),
    show: {
      get() {
        return this.getShowForgotPassword;
      },
      set(v) {
        this.setShowForgotPassword(v);
      }
    }
  },
  watch: {
    show(n, o) {
      if (!n && this.$refs.forgotPassword) {
        this.$refs.forgotPassword.resetFields();
      }
    }
  },
  methods: {
    ...mapMutations("auth", ["setShowForgotPassword"]),
    resetSuccess() {
      this.setShowForgotPassword(false);
    }
  }
};
</script>

