<template>
  <div class="join-component">
    <Select v-model="coinType" size="large" class="select-coin">
      <template v-for="coin in coins">
        <Option :value="coin.key" :key="coin.key" :label="coin.name" />
      </template>
    </Select>

    <Input
      size="large"
      class="wallet-address"
      v-model="walletAddress"
      placeholder="Wallet Address"
    />

    <Button size="large" type="primary">
      Join
    </Button>
  </div>
</template>

<script>
export default {
  name: "join-component",
  computed: {
    coins() {
      return [
        {
          name: "Bitcoin",
          key: "bitcoin"
        }
      ];
    }
  },
  data() {
    return {
      coinType: "bitcoin",
      walletAddress: ""
    };
  }
};
</script>

<style lang="scss" scoped>
.join-component {
  margin: 0px auto;
  width: 550px;
  max-width: 100%;
  display: flex;
}

.select-coin {
  width: 200px;
  margin-right: 5px;
}

.wallet-address {
  margin-right: 5px;
}
</style>
