<template>
  <div class="login-component">
    <i-input class="login-margin" v-model="email" placeholder="Email" />
    <i-input
      class="login-margin"
      v-model="password"
      type="password"
      placeholder="Password"
      @on-enter="login()"
    />
    <a
      @click="passwordReset()"
      class="login-link"
      style="margin:4px;cursor:pointer"
      >Forgot password?</a
    >
    <i-button @click="login()" class="login-margin" type="primary">
      LOGIN
    </i-button>
  </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";
import Utils from "@/helpers/utils";
export default {
  name: "login-component",
  data() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    ...mapMutations("auth", ["setShowLoginModal", "setShowForgotPassword"]),
    ...mapActions("auth", ["firebaseLogin"]),
    login() {
      Utils.showLoading();
      this.firebaseLogin({ email: this.email, password: this.password })
        .then(
          res => {
            console.log({ res });
            this.resetData();
            Utils.hideLoading();
            Utils.showMessage("success login", "success");
            this.setShowLoginModal(false);
          },
          err => {
            Utils.hideLoading();
            Utils.showMessage("error login", "error");
            console.log({ err });
          }
        )
        .catch(err => {
          Utils.showMessage("error login", "error");
          Utils.hideLoading();
        });
    },
    resetData() {
      this.email = "";
      this.password = "";
    },
    passwordReset() {
      this.setShowForgotPassword(true);
      this.setShowLoginModal(false);
    }
  }
};
</script>

<style>
.login-component {
  padding: 0px 8px;
}
.login-margin {
  margin: 8px 0px;
  width: 100%;
}
</style>
