export default {
  name: "get-involved-component",
  components: {
    TitlePageComponent
  },

  computed: {
    items() {
      return [
        {
          title: "MEMBERSHIP",
          description: "Become a member and be part of our movement.",
          iconType: "iview",
          icon: "md-people",
          route: "/membership"
        },
        {
          title: "PARTNERSHIP",
          description: "Be one of our growing number of partners worldwide.",
          iconType: "iview",
          icon: "md-people",
          route: "/partner"
        },
        {
          title: "SPONSORSHIP",
          description: "Support our campaign by becoming a sponsor.",
          iconType: "iview",
          icon: "logo-usd",
          route: "/sponsor"
        },
        {
          title: "STORE",
          description: "Buy our merch to help us fund our projects.",
          iconType: "iview",
          icon: "md-people",
          route: process.env.VUE_APP_STORE_URL,
