    display: flex;
    justify-content: center;
    height: 80px;
    max-width: 340px;
    margin: 0px auto;
  }

  .card-icon {
    font-size: calc(2vw + 40px);
    color: var(--secondary-color);
  }

  .card-detail {
    margin-left: 10px;
  }

  .card-title {
    font-size: calc(0.3vw + 13px);
