<template>
  <div class="client-header">
    <div class="client-header-desktop">
      <router-link to="/home">
        <img class="app-logo" :src="CoinservesLogo" />
      </router-link>

      <div class="client-header-actions">
        <router-link to="/about-us">
          <Button class="button-bold button-white" size="small" type="text"
            >ABOUT US</Button
          >
        </router-link>

        <router-link to="/projects">
          <Button class="button-bold button-white" size="small" type="text"
            >PROJECTS</Button
          >
        </router-link>

        <Dropdown>
          <router-link to="/services">
            <Button class="button-bold button-white" size="small" type="text">
              SERVICES
              <Icon type="md-arrow-dropdown" />
            </Button>
          </router-link>

          <DropdownMenu slot="list">
            <DropdownItem>
              <router-link class="some-class" to="/partner">
                PARTNER
              </router-link>
            </DropdownItem>
            <DropdownItem>
              <router-link class="some-class" to="/sponsor">
                SPONSOR
              </router-link>
            </DropdownItem>
          </DropdownMenu>
        </Dropdown>

        <Dropdown>
          <router-link to="/updates">
            <Button class="button-bold button-white" size="small" type="text">
              UPDATES
              <Icon type="md-arrow-dropdown" />
            </Button>
          </router-link>

          <DropdownMenu slot="list">
            <DropdownItem>
              <router-link class="some-class" to="/upcoming">
                UPCOMING
              </router-link>
            </DropdownItem>
            <DropdownItem>
              <router-link class="some-class" to="/accomplishment">
                ACCOMPLISHMENTS
              </router-link>
            </DropdownItem>
            <DropdownItem>
              <router-link class="some-class" to="/stories">
                STORIES
              </router-link>
            </DropdownItem>
          </DropdownMenu>
        </Dropdown>

        <Dropdown>
          <router-link to="/join-us">
            <Button class="button-bold button-white" size="small" type="text">
              JOIN US
              <Icon type="md-arrow-dropdown" />
            </Button>
          </router-link>

          <DropdownMenu slot="list">
            <DropdownItem>
              <router-link class="some-class" to="/membership">
                MEMBERSHIP
              </router-link>
            </DropdownItem>
            <DropdownItem>
              <router-link class="some-class" to="/volunteer">
                VOLUNTEER
              </router-link>
            </DropdownItem>
            <DropdownItem>
              <router-link class="some-class" to="/fundraising">
                FUNDRAISING
              </router-link>
            </DropdownItem>
          </DropdownMenu>
        </Dropdown>

        <router-link to="/contact-us">
          <Button class="button-bold button-white" size="small" type="text"
            >CONTACT</Button
          >
        </router-link>

        <Button
          class="button-bold"
          type="primary"
          size="large"
          shape="circle"
          :to="storeUrl"
          target="_blank"
        >
          <i class="fas fa-store" />
          STORE
        </Button>
        <Button
          v-if="getCurrentUser"
          @click="logout()"
          style="margin-left:8px"
          size="large"
          type="primary"
          shape="circle"
        >
          <i class="fas fa-store" />
          LOGOUT
        </Button>
        <Button
          v-else
          @click="showLogin()"
          style="margin-left:8px"
          size="large"
          type="primary"
          shape="circle"
        >
          <i class="fas fa-store" />
          LOGIN
        </Button>
      </div>
    </div>

    <div class="client-header-mobile"></div>
    <span v-if="getCurrentUser">
      Welcome <b>{{ getCurrentUser.email }}</b>
    </span>
  </div>
</template>

<script>
import CoinservesLogo from "../../assets/coinserves-logo.png";
import { mapGetters, mapActions, mapMutations } from "vuex";
import db from "@/firebase/db";

export default {
  name: "client-header",
  data() {
    return {
      CoinservesLogo,
      allUsers: []
    };
  },
  firestore() {
    return {
      allUsers: db.getAllUsers()
    };
  },
  computed: {
    ...mapGetters("currentUser", ["getCurrentUser"]),
    storeUrl() {
      return process.env.VUE_APP_STORE_URL;
    }
  },
  methods: {
    ...mapMutations("auth", ["setShowLoginModal"]),
    ...mapActions("auth", ["firebaseLogout"]),
    showLogin() {
      this.setShowLoginModal(true);
    },
    logout() {
      this.firebaseLogout().then(
        res => {
          console.log({ res });
        },
        err => {
          console.log({ err });
        }
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.client-header {
  padding-top: 5px;
  position: relative;
  z-index: 1;

  @media screen and (max-width: 1023px) {
    .client-header-desktop {
      // visibility: hidden;
    }
  }

  .client-header-desktop {
    width: 1024px;
    margin: 0px auto;

    display: flex;
    align-items: center;
    justify-content: space-between;

    .client-header-actions {
      flex-grow: 1;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 10px;
    }

    .app-logo {
      width: 245px;
      height: 82px;
      margin-right: 20px;
    }
  }

  .button-bold {
    font-size: 1.05em !important;
    font-weight: bold !important;
  }

  .button-white {
    color: white !important;

    &:hover {
      color: var(--primary-color) !important;
      background: transparent;
    }
  }
}
</style>
