<template>
  <div class="admin-create-blog">
    <h3>Create Blog</h3>
    <div class="card-wrapper">
      <Row class="full-width" :gutter="20">
        <i-col class="full-height" span="16">
          <Card class="quill-card full-height" dis-hover>
            <Input
              :disabled="isPublishing"
              placeholder="Title"
              v-model="blog.title"
              size="large"
            />
            <div class="quill-container">
              <div ref="quill"></div>
            </div>
          </Card>
        </i-col>
        <i-col span="8">
          <Card dis-hover>
            <div slot="title">
              <small>
                <strong>Publish</strong>
              </small>
            </div>
            <div class="publish">
              <div class="button-controls">
                <Button type="default">Save Draft</Button>
                <Button type="default">Preview</Button>
              </div>
              <div class="publish-container">
                <div style="width: 45%;">
                  <Icon
                    type="ios-calendar-outline"
                    style="margin-right: 5px;"
                  />Publish Date : &nbsp;
                </div>
                <div style="width: 55%;">
                  <Date-picker
                    class="full-width"
                    type="date"
                    format="MMMM dd, yyyy"
                  />
                </div>
              </div>
              <div class="publish-button">
                <Button :loading="isPublishing" @click="publish" type="primary"
                  >Publish</Button
                >
              </div>
            </div>
          </Card>
        </i-col>
      </Row>
    </div>
  </div>
</template>

<script>
import Quill from "quill";
import _ from "lodash";
import { mapActions } from "vuex";
import Utils from "@/helpers/utils";

export default {
  name: "CreateBlog",
  data() {
    return {
      blog: Utils.getBlogPostBase(),
      isPublishing: false,
      quill: {}
    };
  },
  mounted() {
    this.quill = new Quill(this.$refs.quill, {
      modules: {
        toolbar: [
          ["bold", "italic", "underline", "strike"],
          ["blockquote", "code-block"],
          [{ header: 1 }, { header: 2 }],
          [{ list: "ordered" }, { list: "bullet" }],
          [{ script: "sub" }, { script: "super" }],
          [{ indent: "-1" }, { indent: "+1" }],
          [{ direction: "rtl" }],
          [{ size: ["small", false, "large", "huge"] }],
          [{ header: [1, 2, 3, 4, 5, 6, false] }],
          [{ color: [] }, { background: [] }],
          [{ font: [] }],
          [{ align: [] }],
          ["clean"]
        ]
      },
      placeholder: "Content",
      theme: "snow"
    });
  },
  methods: {
    ...mapActions("blog", ["createBlogPost", "deleteBlogPost"]),
    publish: _.debounce(function() {
      this.isPublishing = true;
      this.setQuillEnabled(false);
      this.blog.content = this.quill.container.firstChild.innerHTML;

      this.createBlogPost(this.blog)
        .then(res => {
          Utils.showMessage("Successfully posted", "success");
          this.clearBlog();
        })
        .catch(err => Utils.showMessage("Invalid user credentials", "error"))
        .finally(() => {
          this.isPublishing = false;
          this.setQuillEnabled(false);
        });
    }, 200),
    clearBlog() {
      this.blog = Utils.getBlogPostBase();
      this.quill.deleteText(0, this.quill.getLength());
    },
    setQuillEnabled(enabled) {
      this.quill.enable(enabled);
    }
  }
};
</script>

<style lang="scss" scoped>
.admin-create-blog {
  padding: 10px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.button-controls {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.publish-container {
  display: flex;
  align-items: center;
  div {
    display: flex;
    align-items: center;
  }
}
.publish {
  display: flex;
  flex-direction: column;
}
.publish-button {
  text-align: end;
  margin-top: 10px;
}
.card-wrapper {
  display: flex;
  margin-top: 10px;
  height: 100%;
}
.quill-container {
  height: 100%;
  margin-top: 10px;
}
.quill-card {
  /deep/.ivu-card-body {
    height: 85%;
  }
}
.full-height {
  height: 100%;
}
.full-width {
  width: 100%;
}
</style>
