<template>
  <div class="admin-side-navigation">
    <Menu
      accordion
      :open-names="menu.open"
      :active-name="menu.name"
      width="auto"
    >
      <MenuItem name="dashboard" to="/admin/dashboard">
        <div class="menu-item-wrapper">
          <Icon type="ios-analytics-outline" />
          <span>Dashboard</span>
        </div>
      </MenuItem>
      <Submenu name="blog">
        <template slot="title">
          <div class="menu-item-wrapper">
            <Icon type="ios-chatboxes-outline" />
            <span>Blogs</span>
          </div>
        </template>
        <MenuItem name="blogs" to="/admin/blogs">All Blogs</MenuItem>
        <MenuItem name="new-blog" to="/admin/blogs/new">Create New</MenuItem>
        <MenuItem name="archived-blogs" to="/admin/blogs/deleted"
          >Archived</MenuItem
        >
      </Submenu>
      <!-- <MenuItem >
        <div class="menu-item-wrapper">
          <Icon type="ios-chatboxes-outline" />
          <span>Blogs</span>
        </div>
      </MenuItem>-->
      <MenuItem name="events" to="/admin/events">
        <div class="menu-item-wrapper">
          <Icon type="ios-calendar-outline" />
          <span>Events</span>
        </div>
      </MenuItem>
      <MenuItem name="members" to="/admin/members">
        <div class="menu-item-wrapper">
          <Icon type="ios-people-outline" />
          <span>Members</span>
        </div>
      </MenuItem>
      <MenuItem name="announcements" to="/admin/announcements">
        <div class="menu-item-wrapper">
          <Icon type="ios-radio-outline" />
          <span>Announcements</span>
        </div>
      </MenuItem>
      <MenuItem name="settings" to="/admin/settings">
        <div class="menu-item-wrapper">
          <Icon type="ios-settings-outline" />
          <span>Settings</span>
        </div>
      </MenuItem>
      <div class="ivu-menu-item" @click="logout">
        <div class="menu-item-wrapper">
          <Icon type="ios-exit-outline" />
          <span>Logout</span>
        </div>
      </div>
    </Menu>
  </div>
</template>

<script>
import Utils from "@/helpers/utils";
import { mapActions } from "vuex";
export default {
  name: "SideNavigation",
  computed: {
    menu() {
      let openName;

      switch (this.$route.name.toLowerCase()) {
        case "blogs":
        case "new-blog":
        case "archived-blogs":
          openName = "blog";
          break;
      }

      return {
        name: this.$route.name.toLowerCase(),
        open: [openName]
      };
    }
  },
  methods: {
    ...mapActions("auth", ["firebaseLogout"]),
    logout() {
      Utils.showLoading();
      this.firebaseLogout()
        .then(res => this.$router.replace("/admin/login"))
        .catch(err => Utils.showMessage("Unable to logout", "error"))
        .finally(() => Utils.hideLoading());
    }
  }
};
</script>

<style lang="scss" scoped>
.admin-side-navigation {
  height: 100%;
  ul {
    height: 100%;
  }
}
.menu-item-wrapper {
  display: flex;
  align-items: center;
  i {
    margin-right: 10px;
  }
}
</style>
