<template>
  <div class="signup-component">
    <i-input class="signup-margin" v-model="email" placeholder="Email" />
    <i-input
      class="signup-margin"
      v-model="password"
      type="password"
      placeholder="Password"
      @on-enter="signup()"
    />
    <i-button @click="signup()" class="signup-margin" type="primary">
      SIGNUP
    </i-button>
  </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";
import Utils from "@/helpers/utils";

export default {
  name: "signup-component",
  data() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    ...mapMutations("auth", ["setShowLoginModal"]),
    ...mapActions("auth", ["firebaseSignup"]),
    signup() {
      Utils.showLoading();
      this.firebaseSignup({ email: this.email, password: this.password })
        .then(
          res => {
            console.log({ res });
            this.resetData();
            Utils.hideLoading();
            Utils.showMessage("success signup", "success");
            this.setShowLoginModal(false);
          },
          err => {
            Utils.hideLoading();
            Utils.showMessage("error signup", "error");
            console.log({ err });
          }
        )
        .catch(err => {
          Utils.showMessage("error signup", "error");
          Utils.hideLoading();
        });
    },
    resetData() {
      this.email = "";
      this.password = "";
    }
  }
};
</script>

<style>
.signup-component {
  padding: 0px 8px;
}
.signup-margin {
  margin: 8px 0px;
  width: 100%;
}
</style>
