<template>
  <div class="admin-login">
    <div class="login-bg" />
    <div class="login-card-container">
      <router-link to="home">
        <img
          draggable="false"
          class="app-logo"
          src="../../assets/coinserves-logo.png"
        />
      </router-link>
      <Card class="login-card">
        <div class="login-container">
          <Input
            size="large"
            v-model="auth.email"
            prefix="ios-mail-outline"
            type="email"
            placeholder="Email address"
            :disabled="isLoggingIn"
          />
          <Input
            size="large"
            v-model="auth.password"
            prefix="ios-lock-outline"
            type="password"
            placeholder="Password"
            @on-enter="login"
            :disabled="isLoggingIn"
          />
          <Checkbox v-model="rememberMe">Remember me</Checkbox>
          <Button
            :disabled="isLoggingIn"
            :loading="isLoggingIn"
            @click="login"
            type="primary"
            >LOGIN</Button
          >
          <span class="forgot-password">
            Forgot
            <a @click="setShowForgotPassword(true)">Password</a>
            ?
          </span>
          <small class="version">
            {{ version }}
          </small>
        </div>
      </Card>
    </div>
    <ModalHolder />
  </div>
</template>

<script>
import _ from "lodash";
import { mapActions, mapMutations } from "vuex";
import PackageJSON from "../../../package.json";
import Utils from "@/helpers/utils";
import ModalHolder from "@/components/commons/ModalHolder";

export default {
  name: "Login",
  components: { ModalHolder },
  data() {
    return {
      auth: {
        email: "",
        password: ""
      },
      rememberMe: false,
      isLoggingIn: false
    };
  },
  computed: {
    version() {
      return PackageJSON.version;
    }
  },
  methods: {
    ...mapMutations("auth", ["setShowLoginModal", "setShowForgotPassword"]),
    ...mapActions("auth", ["firebaseLogin", "firebasePasswordReset"]),
    clearLogin() {
      this.auth.email = "";
      this.auth.password = "";
    },
    login: _.debounce(function() {
      this.isLoggingIn = true;
      this.firebaseLogin({
        email: this.auth.email,
        password: this.auth.password
      })
        .then(res => {
          this.clearLogin();
          this.$router.replace("/admin/dashboard");
        })
        .catch(err => {
          Utils.showMessage("Invalid user credentials", "error");
        })
        .finally(() => {
          this.isLoggingIn = false;
        });
    }, 200)
  }
};
</script>

<style lang="scss" scoped>
.admin-login {
  height: 100vh;
  width: 100vw;
}
.forgot-password {
  text-align: center;
}
.app-logo {
  width: 300px;
  margin-bottom: 20px;
}
.login-card-container {
  position: absolute;
  z-index: 1;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login-card {
  width: 350px;
}
.login-container > *:not(:last-child) {
  margin-bottom: 10px;
}
.login-container:first-child {
  margin-top: 5px;
}
.login-container {
  display: flex;
  flex-direction: column;
}
.login-bg {
  background-image: url("../../assets/images/green.jpg");
  background-size: cover;
  background-position: center center;
  filter: brightness(0.8);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.version {
  text-align: end;
  color: #c3c3c3;
}
</style>
